














.welcome{
  max-width: 400px;
  margin-top: 100px;
}
.welcome h2{
  font-size: 3em;
}
.welcome input{
  width: 50%;
}
.welcome button{
  margin: 30px auto;
}

.chat h2{
    font-size: 2.6em;
    margin-bottom: 40px;
}
.chat span{
    font-size: 1.4em;
}
.chat .time{
    font-size: 0.8em;
}
.messages{
    max-height: 300px;
    overflow: auto;
}
.messages::-webkit-srollbar{
    width: 3px;
}
.messages::-webkit-srollbar-track{
    background: #ddd;
}
.messages::-webkit-srollbar-thumb{
    background: #aaa;
}
